*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.container {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 1rem;
}

.site-header {
  background-color: #ffffff;
  border-bottom: 1px solid #eaeaea;
  padding: 1rem 0;
}
.site-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.site-header .logo {
  height: 48px;
}
.site-header .nav-list {
  display: flex;
  gap: 1.5rem;
  list-style: none;
}
.site-header .nav-list a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
}
.site-header .nav-list a:hover {
  color: #007acc;
}

.button {
  background: #007bff;
  color: #fff;
  padding: 0.75rem 1.25rem;
  border: none;
  border-radius: 5px;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero img {
  max-width: 100%;
}
.hero h1 {
  font-size: 2rem;
  margin-top: 1rem;
}

.form-group {
  margin-bottom: 1rem;
}
.form-group label {
  display: block;
  margin-bottom: 0.5rem;
}
.form-group input {
  padding: 0.5rem;
  width: 100%;
}

body {
  font-family: sans-serif;
}